.feedback-form {
  max-width: 400px;
  background-color: teal;
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 15px;
  display: flex;
}

.feedback-form button {
  font-size: inherit;
  padding: 12px;
}

input, textarea {
  padding: 7px;
}

textarea {
  resize: none;
}

